import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private lang: LanguageService
  ) {
    this.keySearch = this.router.getCurrentNavigation().extras.state.keySearch;
    this.sortby = this.router.getCurrentNavigation().extras.state.sortby;
  }

  addWallet: FormGroup;
  keySearch: any;
  sortby: any;
  ngOnInit(): void {
    this.initForm();
  }

  /**
   * khởi tạo form có các trường được required trong bill
   * @example initForm()
   */
  initForm() {
    this.addWallet = new FormGroup({
      name: new FormControl('', Validators.required),
      bank_name: new FormControl('', Validators.required),
      number: new FormControl('', [Validators.required, Validators.minLength(6)]),
      holder: new FormControl('', Validators.required),
      note: new FormControl(''),
      address: new FormControl(''),
      id_branch: new FormControl(this.vhQuerySales.getDefaultBranch()._id),
      delete_hidden: new FormControl(false),
    });
  }

  /**
   * @param formValue
   * @example this.addNewAddwallet({name : 'abc'})
   */
  addNewAddwallet(formValue) {
    this.vhComponent.showLoading("").then(() => {
      this.vhQuerySales.addWallet(formValue)
        .then((res) => {
          this.vhComponent.hideLoading(0)
          setTimeout(() => {
            this.vhComponent.showToast(1500, `${this.lang.translate("Payment account")} ${formValue.bank_name} ${this.lang.translate("has been added successfully")}`, "success-toast")
            this.goBack();
          }, 500)
        }, err => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(1500, this.lang.translate("An eror occured while adding payment account. Please try again"), "alert-toast")
        })
    })
  }

  /**
   * router về trang danh sách tài khoản
   * @example this.goBack()
   */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/cashflow/wallet'], {
      state: {
        keySearch: this.keySearch,
        sortby: this.sortby
      }
    });
  }

}
